.home_main {
  display: flex;
  margin: auto;
}
.home_left_bar {
  height: 40vh;
  width: 14%;
  position: fixed;
  background-color: white;
  border: 1px solid;
  border-bottom-left-radius: 1.2rem;
  border-bottom-right-radius: 1.2rem;
}

.type_of_product {
  font-size: 24px;
  width: 70%;
  margin: auto;

  margin-top: 3vh;
  padding-left: 1%;
  padding-right: 1%;
  border-radius: 1.3rem;
  color: aliceblue;

  background-image: url(../../images/bark.png);
}
.typebox_small {
  display: none;

  padding-left: 8%;
  padding-right: 8%;
  font-size: 16px;
  position: fixed;
  justify-content: space-between;
  background-image: url(../../images/bark.png);
  align-items: center;
}
.type_of_product_small {
  font-size: 18px;
}
.type_of_product_small:hover {
  cursor: pointer;
}
.type_of_product:hover {
  cursor: pointer;
}
.home_right_main {
  width: 78%;
  display: flex;
  flex-direction: column;
  margin-left: 20%;
}
.home_heading_box {
  width: 49%;
  height: 5vh;
  font-size: 18px;
  margin: auto;
  border-radius: 2.1rem;
  background-image: url(../../images/bark.png);
  margin-bottom: 1%;
  margin-top: 2%;

  font-style: initial;
  color: rgb(255, 255, 255);

  padding: 0.5%;
  padding-right: 0.5%;
}
.home_heading_box > h2 {
  font-weight: 700;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  margin-bottom: 1vh;
}

.home_right_slider {
  border: 2px solid black;
  background-image: url(../../images/slide_1.jpg);
  width: 80%;
  height: 65vh;
  border-radius: 1.2rem;
  margin-left: 8%;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px,
    rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
  background-size: 100% 100%;
  animation: changeImage 20s 2s alternate infinite;
}
@keyframes changeImage {
  0% {
    background-image: url(../../images/slide_1.jpg);
  }
  25% {
    background-image: url(../../images/slide_2.jpg);
  }
  50% {
    background-image: url(../../images/slide_3.jpg);
  }
  75% {
    background-image: url(../../images/slide_4.jpg);
  }
  100% {
    background-image: url(../../images/slide_5.png);
  }
}

.Eco_kitchen {
  font-size: 1.1rem;
  text-align: center;

  /* margin-right: 80%; */
  width: 120px;
  margin-top: 3vh;
  margin-bottom: 1vh;
  padding-left: 1%;
  padding-right: 1%;
  border-radius: 1.3rem;
  color: aliceblue;
  background-image: url(../../images/bark.png);
}
.Eco_kitchen_box {
  display: grid;
  
  grid-template-columns: repeat(5, 1fr);
  gap: 1rem;
}

.home_left_bar > div {
  height: 14%;
}

@media all and (min-width: 1031px) {
  .Eco_kitchen_box > div {
    height: 48vh;
  }
}
@media all and (min-width: 1031px) and (max-width: 1300px) {
  .home_left_bar {
    height: 35vh;
  }

  .Eco_kitchen {
    margin-right: 85%;
  }
  .home_heading_slogan {
    font-size: 18px;
  }
  .type_of_product {
    font-size: 17px;
  }
  .Eco_kitchen_box > div {
    height: 46vh;
  }
}

@media all and (min-width: 701px) and (max-width: 1030px) {
  .home_right_slider {
    height: 40vh;
  }
  .Eco_kitchen {
    margin-right: 85%;
  }
  .home_heading_box {
    height: 5vh;
    font-size: 17px;
  }

  .home_left_bar {
    width: 20%;
  }
  .home_right_main {
    width: 76%;
    margin-left: 23%;
  }
  .Eco_kitchen_box {
    grid-template-columns: repeat(3, 1fr);
  }
  .type_of_product {
    font-size: 22px;
  }
}
